import React, { useState } from "react";
import { dbService } from "fbase";
import AutoHeightTextarea from "./AutoHeightTextarea";
import CanvasDraw from "react-canvas-draw";

const DiaryFactory = ({ saveableCanvas, title, setTitle, strong, setStrong, deep, setDeep, fast, setFast, diary, setDiary, saveData, userObj, attachment, setAttachment, saveImage, setSaveImage, makeNew, diaryObj, setClear, onClick }) => {
    const [global, setGlobal] = useState(false);
    // input   
    const onErase = () => {
        saveableCanvas.eraseAll();
    };
    const onUndo = () => {
        saveableCanvas.undo();
    }
    const onGlobal = () => {
        setGlobal(prev => !prev);
    }
    const onChange = (event, setTitle) => {
        const {
          target: { value },
        } = event;
        setTitle(value);
        console.log(value);
    };

    // diaryFactory onSubmit
    const onSubmit = async (event) => {
        event.preventDefault();
        saveImage = saveableCanvas.getSaveData(); // edit   
        attachment = saveableCanvas.getDataURL(); // url 
        setAttachment(attachment);
        setSaveImage(saveImage);
        let nowDate = new Date();
        const diaryObj = {
            title: title,
            strong: strong,
            deep: deep,
            fast: fast,
            text: diary,
            createdAt: nowDate,
            date: `${nowDate.getFullYear()} ${nowDate.getMonth()+1} ${nowDate.getDate()}`,
            createrId: userObj.uid,
            attachmentUrl: attachment,
            saveImage: saveImage,
            global: global
        }
        await dbService.collection("diaries").add(diaryObj);
        setTitle(""); //   
        setDiary(""); //   
        setClear(false);
    };

    const onEditSubmit = async (event) => {
        event.preventDefault();
        //   
        saveImage = saveableCanvas.getSaveData(); // edit   
        attachment = saveableCanvas.getDataURL(); // url 
        setAttachment(attachment);
        setSaveImage(saveImage);
        // 
        await dbService.doc(`diaries/${diaryObj.id}`).update({
            title: title,
            strong: strong,
            deep: deep,
            fast: fast,
            text: diary,
            attachmentUrl: attachment,
            saveImage: saveImage,
            global: global,
        })
        setClear(false);
    }
    return(
        <>
        <div class="bg" />
        <div class="form-wrap">
        <div class="button-wrap">
            <button onClick={onErase}></button>
            <button onClick={onUndo}></button>
            <button onClick={onGlobal}>{global ? "": ""}</button>
            <button class="initial" onClick={onClick}>X</button>
        </div>
        <form onSubmit={makeNew ? onSubmit : onEditSubmit}>
            <input
              type="text"
              placeholder="  & "
              value={title}
              required
              onChange={(event)=>{onChange(event,setTitle)}}
            />
            <CanvasDraw 
            ref={(canvasDraw)=>(saveableCanvas = canvasDraw)}
            saveData={saveData}
            canvasWidth={300}
	        canvasHeight={200}
            brushRadius={5}
            enablePanAndZoom
            style={{padding:'20px 0'}}
            />
            <h4>
                <span> </span>
                <select name="strong" onChange={(event)=>{onChange(event,setStrong)}} value={strong}>
                    <option value=""></option>
                    <option value=""></option>
                </select>
            </h4>
            <h4>
                <span> </span>
                <select name="deep" onChange={(event)=>{onChange(event,setDeep)}} value={deep}>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
            </h4>
            <h4>
                <span> </span>
                <select name="fast" onChange={(event)=>{onChange(event,setFast)}} value={fast}>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
            </h4>
            <AutoHeightTextarea diary={diary} setDiary={setDiary} />
            <input type="submit" value="Save" />
        </form>
        </div>
        </>
    )

}
export default DiaryFactory;