import { useState } from "react";

const AutoHeightTextarea = ({diary, setDiary}) => {
    //    Dictionary
    const [lineBreakIndexDict, setLineBreakIndexDict] = useState({});
    //   ()
    const [lineHeight, setLineHeight] = useState(0);

    //      
    const checkItemChangeHandler = (event) => {
        const {
            target:{value},
        } = event;
        setDiary(value);
        // Scroll  line break
        if (event.target.scrollHeight !== event.target.clientHeight) {
            setLineHeight(prev => prev+1);	// textarea  
            setLineBreakIndexDict({...lineBreakIndexDict, [event.target.value.length-1]: 1});	//   
        }
        else {
	    //     line break 
            if (lineBreakIndexDict[event.target.value.length]) {
                setLineHeight(prev => prev-1);	// textarea  
                setLineBreakIndexDict({...lineBreakIndexDict, [event.target.value.length]: 0});	// Dictionary 
            }
        }
    }

    //           
    const checkItemEnterHandler = (event) => {
        if (event.key === 'Enter') {
            // textarea  checkItemChangeHandler 
            setLineBreakIndexDict({...lineBreakIndexDict, [event.target.value.length]: 1}); //   
        }
    }    
    return (
        <div className="autoHeightTextarea">
        <textarea
            type='text'
            value={diary}
            placeholder="  "
            onChange={checkItemChangeHandler}
            onKeyDown={checkItemEnterHandler}
            style={{
                width: '100%',
                height: ((lineHeight * 70) + 70) + 'px'}}
        />
    </div>
    )
}

export default AutoHeightTextarea;